import { createSlice } from "@reduxjs/toolkit";

// 1. 创建一个slice
const counter2Slice = createSlice({
    name: 'counter2',
    initialState: {
        v:100
    },
    reducers: {
        counterAdd(state) {
            state.v +=1
        }
    }
})

export const {counterAdd} = counter2Slice.actions //2. 把slice的actions导出去，这样可以通过actions修改这个slice的状态；

export default counter2Slice.reducer //3.. 导出reducer放到全局的store中去，这样全局才可以使用这个slice